import {
  ButtonStyled,
  ItemInfoStyled,
  ListItemStyled,
  ListStyled,
} from './ContactList.styled';

export function ContactList({ filteredContacts, onDelete, deleteAll }) {
  return (
    <ListStyled>
      {filteredContacts.map(({ name, number, id }) => (
        <ListItemStyled key={id} name={name}>
          <ItemInfoStyled>
            <span>{name}:</span>
            <span> {number}</span>
          </ItemInfoStyled>
          <ButtonStyled
            type="button"
            name={id}
            onClick={e => onDelete(e.target.name)}
          >
            delete
          </ButtonStyled>
        </ListItemStyled>
      ))}
    </ListStyled>
  );
}
